import React from 'react';
import {Grid, Row, Col, Glyphicon} from 'react-bootstrap';

import schedules from './schedules.json';


const ScheduleTeaser = ({schedule}) => (
  <Row className="clearfix schedule-item">
    <Col sm={2} md={2}>
      <h4 className="date"><Glyphicon glyph="calendar" className="highlight-text"/>Tháng {schedule.date}</h4>
    </Col>
    <Col md={7}>
      <h3 className="highlight-text text-capitalize"><a href={`/#/courses/${schedule.courseId}`}>{schedule.name}</a></h3>
      <p><Glyphicon glyph="map-marker" className="highlight-text"/>{schedule.location}</p>
      <p>{schedule.teaser}
        <a href={`/#/courses/${schedule.courseId}`} className="more-link"><Glyphicon glyph="plus"/>Chi tiết</a>
      </p>
    </Col>
    <Col md={3}>
      <h4><Glyphicon glyph="thumbs-up" className="highlight-text" />Đối tượng tham gia</h4>
      <p>{schedule.who}</p>
    </Col>
  </Row>
);

const ScheduleTeasers = ({titled, limit}) => {
  return (
    <div className="schedules">
      <Grid className="space-bottom">
        {schedules.slice(0).reverse().map(c => <ScheduleTeaser key={c.id} schedule={c} />)}
      </Grid>
    </div>
  );
};

export default ScheduleTeasers;



