import React from 'react';
import {Glyphicon} from 'react-bootstrap';

const Banner = () => {
  return (
    <div id="sticky-banner">
      <div className="content-wrapper">
        <div className="news">
          <div className="news-content">
            <a href="#/membership"><h4 className="text-uppercase banner-title">Lịch hoạt động CLB hội viên PDI tháng 7, 8 <Glyphicon glyph="share-alt" /></h4></a>
            <p className="collapse-content banner-body">Tự làm đồ thủ công, mỹ thuật sáng tạo, biểu diễn văn nghệ… làm quà tặng và gây quỹ quà tặng cho Trung tâm Bảo trợ xã hội địa bàn Hà Nội.</p>
          </div>
        </div>
        <div className="news">
          <div className="news-content">
            <h4 className="text-uppercase banner-title"><Glyphicon glyph="star" />Lớp học khai giảng trong tháng 8<Glyphicon glyph="star" /></h4>
            <p className="text-capitalize banner-body">Giáo dục về giới tính và phòng chống xâm hại</p>
          </div>
        </div>
        <div className="news">
          <div className="news-content">
            <h4 className="text-uppercase banner-title"><Glyphicon glyph="star" />Lớp học khai giảng trong tháng 8<Glyphicon glyph="star" /></h4>
            <p className="text-capitalize banner-body">Hành trang bạn gái tuổi teen</p>
          </div>
        </div>
        <div className="news">
          <div className="news-content">
            <h4 className="text-uppercase banner-title"><Glyphicon glyph="star" />Lớp học khai giảng trong tháng 8<Glyphicon glyph="star" /></h4>
            <p className="text-capitalize banner-body">Tôi khám phá khoa học</p>
          </div>
        </div>
      </div>
  </div>
  );
};

export default Banner;



