import React, {Component} from 'react';
import {Route} from 'react-router-dom';

import ScheduleTeasers from './ScheduleTeasers';

class SchedulesPage extends Component {

  componentDidMount () {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div id="schedules-list" className="star-bg">
        <h2 className="title-underline uppercase">Lịch khai giảng các khoá học của PDI</h2>
        <ScheduleTeasers limit={-1} />
      </div>
    );
  }
}

SchedulesPage.route = <Route exact path="/schedules" component={SchedulesPage}/>;

export default SchedulesPage;



